














import {Component, Emit, Prop, Vue} from "vue-property-decorator";

@Component({
})
export default class Paginate extends Vue {
    @Prop({
        required: true
    })
    totalPages!: number
    @Prop({
        required: true
    })
    totalItems!: number
    @Prop({
        required: true
    })
    itemsOnPage!: number
    @Prop({
        required: true
    })
    value!: number

    get allowNextPage (): boolean {
        return this.nextPage <= this.totalPages
    }

    get allowPrevPage (): boolean {
        return this.prevPage > 0
    }
    get prevPage (): number {
        return this.value - 1
    }
    get nextPage (): number {
        return this.value + 1
    }

    @Emit('input')
    changePage(page: number): number {
        return page
    }
}
