






import {Component, Prop, Vue} from "vue-property-decorator";

@Component({})
export default class Preloader extends Vue {
    @Prop({
        type: Boolean,
        default: () => false,
        required: false
    })
    md!: boolean
    @Prop({
        type: Boolean,
        default: () => false,
        required: false
    })
    sm!: boolean
    @Prop({
        type: Boolean,
        default: () => false,
        required: false
    })
    xs!: boolean
    @Prop({
        type: Boolean,
        default: () => false,
        required: false
    })
    lg!: boolean

    get size(): string {
        if (this.lg) {
            return `100px`
        } else if (this.md) {
            return `75px`
        } else if (this.sm) {
            return `50px`
        } else if (this.xs) {
            return `25px`
        }

        return ''
    }
}
